@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.list-item {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .list-item * > {
    flex-grow: 1;
    flex-shrink: 1; }
  .list-item__title {
    width: calc(100% - 30px);
    padding: 8px; }
  .list-item__del {
    display: block;
    font-size: 1rem;
    border: none;
    background-color: transparent;
    width: 20px;
    color: white;
    cursor: pointer; }

input .list-item__native-input {
  background-color: #646363;
  cursor: pointer; }

.task-list {
  max-width: 980px;
  margin: 0 auto;
  padding: 1rem 1rem;
  color: white;
  box-shadow: 0 2px 3px 2px rgba(0, 0, 0, 0.2);
  background-color: #0e181a; }
  .task-list__counter {
    font-size: 1.1rem;
    font-weight: 500;
    text-transform: uppercase; }
  .task-list__btn {
    color: white;
    background-color: black;
    border: none;
    border: 2px solid transparent;
    font-size: 12px;
    padding: 3px;
    cursor: pointer; }

input.task-list__input {
  background-color: #646363;
  border: none;
  padding: 4px; }

* {
  box-sizing: border-box; }

html {
  font-family: 'Raleway', sans-serif;
  font-size: 14px; }

body {
  max-width: 2300px;
  margin: auto; }

h1,
h2,
h3,
h4,
h5,
p, figure, a {
  margin: 0;
  padding: 0; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

a {
  text-decoration: none;
  display: inline-block;
  color: #0cbde0; }

button:focus {
  outline: none; }

input:focus {
  outline: none; }

input {
  color: white; }

input::-webkit-input-placeholder {
  color: white; }

input:-ms-input-placeholder {
  color: white; }

input::-ms-input-placeholder {
  color: white; }

input::-moz-placeholder {
  color: white; }

input::placeholder {
  color: white; }

.page-login {
  background-image: url(/to-do-test-app/static/media/todo-list.3e773a8a.jpg);
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; }

.login-container {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  font-size: 1.5rem;
  color: white;
  justify-content: center; }
  .login-container__caption {
    font-size: 3rem; }

.btn-container {
  text-align: center;
  font-size: 2rem; }

.btn {
  position: relative;
  display: block;
  margin: 1rem auto;
  padding: 25px 60px;
  overflow: hidden;
  border-width: 0;
  font-size: 1.4rem;
  text-transform: uppercase;
  outline: none;
  cursor: pointer;
  border-radius: 2px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.6);
  background-color: #0E181A;
  color: white;
  -webkit-transition: background-color .3s;
  transition: background-color .3s; }
  .btn:hover, .btn:focus {
    background-color: #27ae60; }

.logout-container {
  color: white;
  display: flex;
  flex-direction: column;
  margin: 2rem;
  justify-content: flex-end;
  align-items: center; }
  .logout-container__btn {
    padding: 10px 15px;
    font-size: 1rem; }
  .logout-container__usere {
    font-size: 2rem;
    font-weight: 600;
    padding-bottom: 1rem; }

.text-center-block {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #0E181A;
  color: white; }

